.stars {
  display: flex;

  &__star {
    margin-right: 4px;
    height: 16px;
    width: 16px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url(/images/star.svg);

    &:last-child {
      margin-right: 0;
    }
  }

  @for $number from 0 through 5 {
    &--#{$number} :nth-child(-n + #{$number}) {
      background-image: url(/images/star-active.svg);
    }
  }
}
